.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  margin-left: 50px;
  margin-right: 50px;
  display: block;
}

.header__logo-img {
  vertical-align: middle;
}

.header__nav {
  margin: 0 50px 0 0;
  display: flex;
}

.header__nav-list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-item {
  margin-right: 20px;
}

.header__nav-item:last-child {
  margin-right: 0;
}

.header__nav-link {
  color: #060b35;
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition-duration: .3s;
  display: block;
}

.header__nav-link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition-duration: .3s;
}

.card__description {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  margin: 0 0 24px;
  display: flex;
}

.card__review-number {
  margin: 0;
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin: 0 0 16px;
  display: flex;
}

.card__price-span {
  color: #616070;
  font-size: 12px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__btn {
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition-duration: .3s;
  display: block;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  scale: 120%;
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  align-items: center;
  gap: 4px;
  margin: 0;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

html {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}
